        methods: {
            initChart() {
                let _this = this;
                this.mapChart = echarts.init(document.getElementById(this.id));
                var convertData = function(data) {
                    var res = [];
                    for (var i = 0; i < data.length; i++) {
                        let geoCoord = _.find(_this.cityMap, (item)=>{
                            return data[i][0].name === item.name
                        })
                        if(geoCoord){
                            res.push({
                                name:data[i][0].name,
                                value:geoCoord.data.features[0].properties.cp.concat(data[i][0].value)
                            })
                        }
                    }

                    return res;
                };

                echarts.registerMap(this.cityMap[this.active].code,this.cityMap[this.active].data,{});
                this.mapChart.setOption({
                    tooltip: {
                        show:true,
                        trigger: 'item',
                        triggerOn: 'mousemove',
                        backgroundColor: 'transparent',
